<template>
  <div id="q-app">
    <q-layout view="lHh Lpr fff">
      <q-page
        class="window-height window-width row justify-center items-center"
        style="background: linear-gradient(#ccebff, #99d6ff)"
      >
        <div class="column q-pa-md">
          <div class="row">
            <q-card
              square
              class="shadow-24"
              style="width: 330px; height: 550px"
            >
              <q-card-section class="bg-blue-4">
                <q-item-label class="text-white q-my-md">{{
                  title
                }}</q-item-label>
              </q-card-section>
              <q-card-section v-if="take_exam == false">
                <!-- <q-fab
                  color="primary"
                  @click="switchTypeForm"
                  icon="add"
                  class="absolute"
                  style="top: 0; right: 12px; transform: translateY(-50%)"
                >
                </q-fab> -->
                <q-form class="q-px-sm q-pt-xs">
                  <q-input
                    ref="email"
                    square
                    clearable
                    v-model="email"
                    type="email"
                    lazy-rules
                    dense
                    :rules="[this.required, this.isEmail, this.short]"
                    label="Email"
                  >
                    <template v-slot:prepend>
                      <q-icon name="email" />
                    </template>
                  </q-input>
                  <q-input
                    ref="username"
                    v-if="register"
                    square
                    clearable
                    v-model="name"
                    lazy-rules
                    dense
                    :rules="[this.required, this.short]"
                    type="username"
                    label="Username"
                  >
                    <template v-slot:prepend>
                      <q-icon name="person" />
                    </template>
                  </q-input>
                  <q-input
                    ref="password"
                    square
                    clearable
                    v-model="password"
                    :type="passwordFieldType"
                    lazy-rules
                    dense
                    :rules="[this.required, this.short]"
                    label="Password"
                  >
                    <template v-slot:prepend>
                      <q-icon name="lock" />
                    </template>
                    <template v-slot:append>
                      <q-icon
                        :name="visibilityIcon"
                        @click="switchVisibility"
                        class="cursor-pointer"
                      />
                    </template>
                  </q-input>
                  <q-input
                    ref="password_confirmation"
                    v-if="register"
                    square
                    clearable
                    v-model="password_confirmation"
                    :type="passwordFieldType"
                    lazy-rules
                    dense
                    :rules="[this.required, this.short, this.diffPassword]"
                    label="Password"
                  >
                    <template v-slot:prepend>
                      <q-icon name="lock" />
                    </template>
                    <template v-slot:append>
                      <q-icon
                        :name="visibilityIcon"
                        @click="switchVisibility"
                        class="cursor-pointer"
                      />
                    </template>
                  </q-input>
                </q-form>
              </q-card-section>
              <q-card-section v-if="take_exam == true">
                <q-form>
                  <p
                    style="
                      color: green;
                      margin-top: -10px;
                      margin-bottom: -10px;
                    "
                    v-if="msg.color == 'success'"
                  >
                    {{ msg.msg }}
                  </p>
                  <p
                    style="color: red; margin-top: -10px; margin-bottom: -10px"
                    v-if="msg.color == 'warning'"
                  >
                    {{ msg.msg }}
                  </p>
           
                  <q-input
                    ref="email"
                    square
                    clearable
                    v-model="email"
                    type="email"
                    lazy-rules
                    dense
                    :rules="[this.isEmail, this.short, this.checkmail]"
                    label="Addessa Internal Email Only"
                  >
                    <template v-slot:prepend>
                      <q-icon name="email" />
                    </template>
                  </q-input>
                  <q-input
                    ref="Full Name"
                    square
                    clearable
                    v-model="fullname"
                    lazy-rules
                    dense
                    :rules="[this.required, this.short]"
                    label="Full Name"
                  >
                    <template v-slot:prepend>
                      <q-icon name="person" />
                    </template>
                  </q-input>

                  <q-select
                    filled
                    v-model="modelModule"
                    :options="optionsModule"
                    use-chips
                    stack-label
                    label="Module"
                  />
                  <q-select
                    filled
                    v-model="modelPosition"
                    :options="optionsPosition"
                    use-chips
                    stack-label
                    label="Position"
                  />
                  <q-select
                    filled
                    v-model="modelBranch"
                    :options="optionsBranch"
                    use-chips
                    stack-label
                    label="Branch"
                  />
                </q-form>
              </q-card-section>

              <q-card-actions class="q-px-lg">
                <q-btn
                  v-if="take_exam == false"
                  unelevated
                  size="lg"
                  color="secondary"
                  @click="btnLabel == 'Login' ? login() : reg()"
                  dense
                  class="full-width text-white"
                  :label="btnLabel"
                />
                <q-btn
                  v-if="take_exam == true"
                  :disable="msg.color == 'success' ? false : true"
                  unelevated
                  size="lg"
                  color="secondary"
                  @click="request()"
                  dense
                  class="full-width text-whit"
                  label="Request Code"
                />
              </q-card-actions>
              <q-card-section v-if="!register" class="text-center q-pa-sm">
                <q-btn
                  v-if="take_exam == true"
                  color="secondary"
                  @click="admin()"
                  label="Admin Access"
                />
                <q-btn
                  v-if="take_exam == false"
                  color="secondary"
                  @click="exam()"
                  label="Take Exam"
                />
                <div class="q-pa-sm"></div>
              </q-card-section>
            </q-card>
          </div>
        </div>
      </q-page>
    </q-layout>
  </div>
</template>
 <script>
import axios from "axios";
import { QSpinnerGears } from 'quasar'
const token = localStorage.getItem("user-token");
const instance = axios.create({
  baseURL: "http://api.laravelbackend.net/api/",
  headers: {
    Authorization: "Bearer " + token,
  },
});

export default {
  data() {
    return {
      title: "Take Exam",
      email: "",
      name: "",
      password: "",
      password_confirmation: "",
      register: false,
      passwordFieldType: "password",
      btnLabel: "Login",
      visibility: false,
      visibilityIcon: "visibility",
      take_exam: true,
      exam_module: [],
      modelModule: null,
      modelBranch: null,
      modelPosition: null,
      optionsModule: [],
      optionsBranch: [],
      optionsPosition: [],
      fullname: null,
      msg: [],
      success: null,
 
    };
  },

  methods: {
    render() {
      instance.get("/create/session/contain").then(
        (res) => {
          this.optionsModule = res.data.module;
          this.optionsBranch = res.data.branches;
          this.optionsPosition = res.data.positions;
        },
        (error) => {
          console.log(error);
        }
      );
    },
    required(val) {
      return (val && val.length > 0) || "Please enter password";
    },
    diffPassword(val) {
      const val2 = this.$refs.password.value;
      return (val && val === val2) || "Password does not match!";
    },
    short(val) {
      return (val && val.length > 3) || "The Value is to short";
    },
    isEmail(val) {
      const emailPattern = /^(?=[a-zA-Z0-9@._%+-]{6,254}$)[a-zA-Z0-9._%+-]{1,64}@(?:[a-zA-Z0-9-]{1,63}\.){1,8}[a-zA-Z]{2,63}$/;
      return emailPattern.test(val) || "Please enter a valid email";
    },
    checkmail() {
      if (!this.$refs.email.hasError) {
        this.check();
      }
    },
    check() {
      instance
        .post("/request/code/checkmail", {
          email: this.email,
        })
        .then(
          (res) => {
            this.msg = res.data;
            this.fullname = res.data.name;
          },
          () => {
            this.msg = { msg: "Email not found on mail DB", color: "warning" };
          }
        );
    },
    submit() {
      if (this.register) {
        this.$refs.email.validate();
        this.$refs.name.validate();
        this.$refs.password.validate();
        this.$refs.repassword.validate();
      } else {
        this.$refs.email.validate();
        this.$refs.password.validate();
      }

      if (!this.register)
        if (!this.$refs.email.hasError && !this.$refs.password.hasError) {
          this.$q.notify({
            icon: "done",
            color: "positive",
            message: "Login",
          });
        }
    },
    switchTypeForm() {
      this.register = !this.register;
      this.title = this.register ? "Create Account" : "Login";
      this.btnLabel = this.register ? "Register" : "Login";
    },
    exam() {
      this.title = "Take Exam";
      this.take_exam = true;
    },
    admin() {
      this.title = "Login";
      this.take_exam = false;
    },
    switchVisibility() {
      this.visibility = !this.visibility;
      this.passwordFieldType = this.visibility ? "text" : "password";
      this.visibilityIcon = this.visibility ? "visibility_off" : "visibility";
    },
    login() {
      this.$refs.email.validate();
      this.$refs.password.validate();
      if (this.$refs.password.validate() && this.$refs.email.validate()) {
        instance
          .post("/login/user", {
            email: this.email,
            password: this.password,
          })
          .then(
            (resp) => {
              const token = resp.data.access_token;
              localStorage.setItem("user-token", token); // store the token in localstorage

              // you have your token, now log in your user :)

              this.$router.push("/");
            },
            (error) => {
              this.$router.push("/");
              this.$q.notify({
                type: "warning",
                message: "" + error.response.data.errors.email[0] + "",
              });

              localStorage.removeItem("user-token"); // if the request fails, remove any possible user token if possible
            }
          );
      }
    },
    request() {
      this.msg = {color: "sending" };
      this.notify();
      instance
        .post("/request/exam/code", {
          email: this.email,
          fullname: this.fullname,
          module: this.modelModule,
          position: this.modelPosition,
          branch: this.modelBranch,
          
        })
        .then((res) => {
          this.$q.notify({
            type: "" + res.data[0].color + "",
            message: "" + res.data[0].msg + "",
            timeout: 50000
          });
          this.msg = {color: "success" };
        
        });
    },
    notify(){
      this.$q.notify({
            spinner: QSpinnerGears,
            message: 'Working...',
            timeout: 13000
          })
    },
    reg() {
      instance
        .post("/register/user", {
          name: this.name,
          email: this.email,
          password: this.password,
          password_confirmation: this.password_confirmation,
        })
        .then(
          () => {
            this.$router.push("/");
          },
          (error) => {
            this.$q.notify({
              type: "warning",
              message: "" + error.response.data.errors.email[0] + "",
            });
            this.$q.notify({
              type: "warning",
              message: "" + error.response.data.errors.name[0] + "",
            });
            this.$q.notify({
              type: "warning",
              message: "" + error.response.data.errors.password[0] + "",
            });
          }
        );
    },
  },
  mounted() {
    this.render();
 
  },
};
</script>
 